// client/src/components/PetCard.js

import React, { useState, useMemo } from "react";
import {
  Card,
  Button,
  Badge,
  Toast,
  ToastContainer,
  OverlayTrigger,
  Tooltip,
  Spinner,
} from "react-bootstrap";
import { Link } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext";
import { useApiCall } from "../hooks/useApiCall";
import { useCart } from "../contexts/CartContext";
import api from "../services/api";
import "./PetImage.css";

const PetCard = ({
  pet,
  onVote,
  onFavorite,
  showAddToFavorites = true,
  showVoting = true,
  showQuickActions = true,
  className = "",
  size = "normal",
}) => {
  const { user } = useAuth();
  const { addToCart } = useCart();
  const [showSuccessToast, setShowSuccessToast] = useState(false);
  const [toastMessage, setToastMessage] = useState("");
  const [imageError, setImageError] = useState(false);

  const { execute: executeVote, loading: voting } = useApiCall({
    onSuccess: () => {
      setToastMessage("Vote recorded successfully!");
      setShowSuccessToast(true);
      setTimeout(() => setShowSuccessToast(false), 3000);
    },
    onError: (error) => console.error("Voting failed:", error),
  });

  const { execute: executeFavorite, loading: favoriting } = useApiCall({
    onSuccess: (result) => {
      setToastMessage(result.isFavorite ? "Added to favorites!" : "Removed from favorites!");
      setShowSuccessToast(true);
      setTimeout(() => setShowSuccessToast(false), 3000);
    },
  });

  const handleVote = async (voteType) => {
    if (!user || voting) return;
    try {
      await executeVote(() => api.post(`/pets/${pet._id}/vote`, { voteType }), "Failed to vote");
      if (onVote) onVote(pet._id, voteType);
    } catch {}
  };

  const handleFavorite = async () => {
    if (!user || favoriting) return;
    try {
      await executeFavorite(() => api.post(`/users/favorites/${pet._id}`), "Favorite failed");
      if (onFavorite) onFavorite(pet._id);
    } catch {}
  };

  const formattedPrice = useMemo(() => {
    return typeof pet.price === "number"
      ? `$${pet.price.toLocaleString()}`
      : pet.price || "Price not set";
  }, [pet.price]);

  const totalVotes = useMemo(() => (pet.votes?.up || 0) + (pet.votes?.down || 0), [pet.votes]);

  const voteRatio = useMemo(() => {
    if (totalVotes === 0) return 0;
    return (((pet.votes?.up || 0) / totalVotes) * 100).toFixed(0);
  }, [pet.votes, totalVotes]);

  const userVote = useMemo(() => {
    if (!user || !user.votedPets) return null;
    const vote = user.votedPets.find((v) => v.pet === pet._id);
    return vote?.voteType || null;
  }, [user, pet._id]);

  const isFavorite = useMemo(() => {
    if (!user || !user.favorites) return false;
    return user.favorites.some((fav) => fav._id === pet._id || fav === pet._id);
  }, [user, pet._id]);

  const cardSizeClasses = {
    small: "pet-card-small",
    normal: "",
    large: "pet-card-large",
  };

  return (
    <>
      <Card className={`h-100 pet-card shadow-sm ${cardSizeClasses[size]} ${className}`}>
        {/* ✅ Image */}
        <div className="pet-image-container">
          {!imageError ? (
            <img
              className="pet-image"
              src={pet.image}
              alt={pet.name}
              loading="lazy"
              onError={(e) => {
                setImageError(true);
                e.target.src = "/images/default.jpg";
              }}
            />
          ) : (
            <div className="image-error-overlay">
              <span><i className="fas fa-image-slash"></i></span>
              <p>Image not available</p>
              <small>Try again later</small>
            </div>
          )}

          <Badge
            bg={pet.available ? "success" : "secondary"}
            className="position-absolute top-0 end-0 m-2"
          >
            {pet.available ? "Available" : "Adopted"}
          </Badge>

          {showAddToFavorites && user && (
            <Button
              variant={isFavorite ? "danger" : "outline-light"}
              size="sm"
              className="position-absolute top-0 start-0 m-2 rounded-circle"
              onClick={handleFavorite}
              disabled={favoriting}
              style={{ width: "35px", height: "35px" }}
            >
              {favoriting ? (
                <Spinner size="sm" />
              ) : (
                <i className={`fas fa-heart ${isFavorite ? "text-white" : "text-dark"}`}></i>
              )}
            </Button>
          )}

          {totalVotes > 0 && (
            <div className="position-absolute bottom-0 start-0 m-2">
              <Badge bg="dark" className="opacity-75">
                {voteRatio}% positive
              </Badge>
            </div>
          )}
        </div>

        <Card.Body className="d-flex flex-column">
          <div className="d-flex justify-content-between align-items-start mb-2">
            <Card.Title className="text-primary mb-0 flex-grow-1" style={{ fontSize: size === "small" ? "1rem" : "1.25rem" }}>
              {pet.name}
            </Card.Title>
            <span className="fw-bold text-success ms-2" style={{ fontSize: size === "small" ? "0.9rem" : "1.1rem" }}>
              {formattedPrice}
            </span>
          </div>

          <div className={`small flex-grow-1 ${size === "small" ? "text-truncate" : ""}`}>
            <div className="mb-1"><strong>Breed:</strong> {pet.breed}</div>
            <div className="mb-1"><strong>Age:</strong> {pet.age}</div>
            {pet.size && (
              <div className="mb-1">
                <strong>Size:</strong>{" "}
                <Badge bg="secondary" className="text-capitalize">
                  {pet.size}
                </Badge>
              </div>
            )}
            {pet.gender && (
              <div className="mb-1">
                <strong>Gender:</strong> {pet.gender}
              </div>
            )}
            {size !== "small" && (
              <div className="mt-2 text-muted" style={{ fontSize: "0.85rem" }}>
                {pet.description?.length > 100
                  ? `${pet.description.substring(0, 100)}...`
                  : pet.description}
              </div>
            )}
          </div>

          {pet.averageRating > 0 && (
            <div className="d-flex align-items-center mb-2">
              <div className="rating-stars me-2">
                {[...Array(5)].map((_, i) => (
                  <i
                    key={i}
                    className={`fas fa-star ${
                      i < Math.floor(pet.averageRating) ? "text-warning" : "text-muted"
                    }`}
                    style={{ fontSize: "0.8rem" }}
                  ></i>
                ))}
              </div>
              <span className="small text-muted">
                {pet.averageRating} ({pet.ratings?.length || 0} reviews)
              </span>
            </div>
          )}

          <div className="mt-auto">
            <Link
              to={`/pet/${pet._id}`}
              className={`btn btn-primary ${size === "small" ? "btn-sm" : ""} w-100 mb-2`}
            >
              <i className="fas fa-eye me-1"></i>
              View Details
            </Link>

            {/* ✅ Only allow cart button for supplies */}
            {pet.type === "supply" && (
              <Button
                variant="warning"
                className={`w-100 ${size === "small" ? "btn-sm" : ""}`}
                onClick={() => addToCart(pet)}
              >
                <i className="fas fa-shopping-cart me-1"></i>
                Add to Cart
              </Button>
            )}

            {showVoting && user && showQuickActions && (
              <div className="d-flex gap-1 mt-2">
                <OverlayTrigger
                  placement="top"
                  overlay={<Tooltip>{userVote === "up" ? "You upvoted" : "Upvote this pet"}</Tooltip>}
                >
                  <Button
                    variant={userVote === "up" ? "success" : "outline-success"}
                    size="sm"
                    onClick={() => handleVote("up")}
                    disabled={voting || !pet.available}
                    className="flex-grow-1"
                  >
                    {voting ? <Spinner size="sm" /> : <><i className="fas fa-thumbs-up"></i> <span className="ms-1">{pet.votes?.up || 0}</span></>}
                  </Button>
                </OverlayTrigger>

                <OverlayTrigger
                  placement="top"
                  overlay={<Tooltip>{userVote === "down" ? "You downvoted" : "Downvote this pet"}</Tooltip>}
                >
                  <Button
                    variant={userVote === "down" ? "danger" : "outline-danger"}
                    size="sm"
                    onClick={() => handleVote("down")}
                    disabled={voting || !pet.available}
                    className="flex-grow-1"
                  >
                    {voting ? <Spinner size="sm" /> : <><i className="fas fa-thumbs-down"></i> <span className="ms-1">{pet.votes?.down || 0}</span></>}
                  </Button>
                </OverlayTrigger>
              </div>
            )}

            {!user && showVoting && (
              <div className="text-center mt-2">
                <small className="text-muted">
                  <Link to="/login">Login</Link> to vote and favorite pets
                </small>
              </div>
            )}
          </div>
        </Card.Body>
      </Card>

      <ToastContainer position="top-end" className="position-fixed p-3">
        <Toast show={showSuccessToast} onClose={() => setShowSuccessToast(false)} bg="success" delay={3000} autohide>
          <Toast.Body className="text-white">
            <i className="fas fa-check-circle me-2"></i>
            {toastMessage}
          </Toast.Body>
        </Toast>
      </ToastContainer>
    </>
  );
};

export const SmallPetCard = (props) => (
  <PetCard size="small" showQuickActions={false} showAddToFavorites={false} {...props} />
);

export const FeaturedPetCard = (props) => (
  <PetCard size="large" className="featured-pet-card border-warning" {...props} />
);

export const SimplePetCard = (props) => (
  <PetCard showVoting={false} showAddToFavorites={false} showQuickActions={false} {...props} />
);

export default PetCard;
